.app-wrapper {
    margin: 0 auto;
    width: 1200px;

    display: grid;
    grid-template-areas:
    "header header"
    "navBar content";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
}

.app-wrapper .app-wrapper-content {
    grid-area: content;
    background-color: cornflowerblue;
}